import * as React from "react";

import { connect } from 'react-redux';
import A from '../components/SheelA';

import * as Logo from '../../img/subox.png';


import { asarVersion, version } from '../../../package.json';



class AboutDialog extends React.Component<{ dispatch: Function, show: boolean }, undefined> {
    componentDidMount() {

    }
    closeHander() {
        this.props.dispatch({
            type: 'dosth',
            do: {
                key: 'aboutDialogDisplay',
                value: false
            }
        })
    }
    render() {
        if (!this.props.show) {
            return null;
        } else {
            return (<div style={{ display: 'block' }} className="reveal-overlay">
                <div style={{ display: 'block' }} className="reveal" id="aboutDialog" data-reveal>
                    <p className="about-title"> Subox</p>
                    <div className="float-left">
                        <img src={Logo} alt="" />
                    </div>
                    <p>
                        Subox
                    </p>
                    <p>
                         <A href="http://www.163sub.com/">163sub</A> 
                    </p>
                    <p>
                        Subox  <A href="https://nodejs.org/zh-cn/">Node.js</A><A href="http://electron.atom.io/">Electron</A> 
                    </p>
                    <p>
                         <A href="https://github.com/zqjimlove/subox">https://github.com/zqjimlove/subox</A>
                    </p>
                    <p className="about-title"></p>
                    <p>
                        
                    </p>
                    <p>
                        <A href="https://inwoo.me">https://inwoo.me</A><br />
                        <A href="mailTo:zqjimlove@gmail.com">zqjimlove@gmail.com</A> <br />
                        Github<A href="https://github.com/zqjimlove">https://github.com/zqjimlove</A>
                    </p>
                    <p>
                         {version} <br/>
                         {asarVersion}
                    </p>
                    <button onClick={this.closeHander.bind(this)} className="close-button" data-close aria-label="Close modal" type="button">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
            </div>);
        }

    }
}


function mapStateToProps(state) {
    return {
        show: state.common.aboutDialogDisplay
    }
}

export default connect(mapStateToProps)(AboutDialog);


